.alert-wrapper {
    position: absolute;
    top: 10px;
    width: 100%;
    animation-name: fadein;
    animation-duration: 1s;
}

.alert-wrapper-hidden {
    animation-name: fadeout;
    animation-duration: 1s;
    position: absolute;
    top: 10px;
    width: 100%;
}

@keyframes fadein {
    0% {
        opacity: 0;
    }
    100% {
        opacity: 1;
    }
}

@keyframes fadeout {
    100% {
        opacity: 0;
    }
    0% {
        opacity: 1;
    }
}
