.task-item {
    padding-top: 10px;
    max-width: 100%
}

.star-icon:hover {
    color: #5046E4;
    cursor: pointer;
}

.star-icon-selected {
    color: #5046E4;
}

.star-icon-selected:hover {
    color: white;
    cursor: pointer;
}

.edit-btn {
    margin: 0 10px;
}

@media (max-width: 650px) {
    .edit-btn {
        transform: translateY(-3px);
    }
}
