.footer {
    position: fixed;
    width: 100%;
    bottom: 0;
    height: 65px;
    padding: 10px 10px 0 10px !important;
}

.social-group {
    display: flex;
    align-items: flex-start;
    justify-content: space-between;
    width: 135px;
    transform: translateY(11px);
}

.social-icon {
    max-width: 30px;
    max-height: 30px;
}

@media (max-width: 650px) {
    .footer {
        height: 130px;
    }
    .footer-item {
        justify-content: center;
        display: flex;
    }
    .social-group {
        width: 100%;
    }

}
